*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --scrore-font-size: 2rem;
  --primary-color: #5b33ee;
  --secondary-color: #eef0f2;
  --font-color: var(--secondary-color);
  --bar-height: 80px;
  --bar-width: 10px;
  --player-bar-margin-left: 10px;
  --computer-bar-margin-right: 10px;
  --score-font-size: 2.5rem;
}

body {
  background: var(--primary-color);
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.score-divider {
  font-size: var(--scrore-font-size);
  font-size: var(--scrore-font-size);
  margin: 0 16px;
  transform: translateY(-8%);
}

.player-score {
  font-size: var(--score-font-size);
  color: var(--secondary-color);
  pointer-events: none;
  position: absolute;
  left: 25%;
  transform: translateX(-50%);
}

.computer-score {
  font-size: var(--score-font-size);
  color: var(--secondary-color);
  pointer-events: none;
  position: absolute;
  left: 75%;
  transform: translateX(-50%);
}

.player-bar {
  width: var(--bar-width);
  height: var(--bar-height);
  background: var(--secondary-color);
  margin-left: var(--player-bar-margin-left);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.computer-bar {
  top: 50%;
  left: calc(100vw - var(--bar-width)  - var(--computer-bar-margin-right));
  width: var(--bar-width);
  height: var(--bar-height);
  background: var(--secondary-color);
  position: absolute;
}

.screen-divider {
  background: var(--secondary-color);
  width: 2px;
  height: 100vh;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
}

.ball {
  --ball-width: 20;
  width: calc(var(--ball-width) * 1px);
  aspect-ratio: 1;
  background: var(--secondary-color);
  border-radius: 50%;
  position: absolute;
}

.player-win-container, .computer-win-container {
  background: var(--secondary-color);
  z-index: 10;
  border-radius: .5rem;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  width: 300px;
  padding: 20px;
  transition: transform .3s;
  display: flex;
  position: fixed;
  transform: translateX(-50%)scale(0);
}

.player-win-container {
  top: 30%;
  left: 25%;
}

.computer-win-container {
  top: 30%;
  left: 75%;
}

.button-container {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.button {
  cursor: pointer;
  z-index: 0;
  background: #fff;
  border: 2px solid #000;
  border-radius: .5rem;
  padding: 10px 20px;
  font-weight: bold;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px #00000080;
}

.button:before {
  content: "";
  z-index: -1;
  background: #ff0;
  transition: transform .5s;
  position: absolute;
  inset: 0;
  transform: translateX(-100%);
}

.button:hover:before {
  transform: translateX(0);
}

.player-win-heading, .computer-win-heading {
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (width <= 760px) {
  .player-win-container, .computer-win-container {
    left: 50%;
  }
}

.overlay {
  opacity: 0;
  pointer-events: none;
  z-index: 9;
  background: #0009;
  transition: opacity .3s;
  position: absolute;
  inset: 0;
}

.player-win-container.active, .computer-win-container.active {
  transform: translateX(-50%)scale(1);
}

.overlay.active {
  opacity: 1;
}

.infinite-mode-toggle-container {
  position: fixed;
  top: -10px;
  left: 5px;
}

#infinite-mode-input {
  appearance: none;
}

#infinite-mode-input + .infinite-mode-label {
  --infinite-mode-label-font-size: 16px;
  --infinite-mode-label-color: #f2db0d;
  --infinite-mode-label-checked-color: #59f20d;
  --infinite-mode-toggle-background: #00000080;
  --infinite-mode-toggle-circle-background: white;
  color: var(--infinite-mode-label-color);
  font-size: var(--infinite-mode-label-font-size);
  align-items: center;
  gap: 5px;
  display: flex;
  position: relative;
}

#infinite-mode-input + .infinite-mode-label:before {
  content: "";
  width: calc(2.5 * var(--infinite-mode-label-font-size));
  height: calc(1 * var(--infinite-mode-label-font-size));
  background: var(--infinite-mode-toggle-background);
  border-radius: 100000000px;
}

#infinite-mode-input + .infinite-mode-label:after {
  content: "";
  width: calc(.8 * var(--infinite-mode-label-font-size));
  aspect-ratio: 1;
  background: var(--infinite-mode-toggle-circle-background);
  left: calc(.25 * var(--infinite-mode-label-font-size));
  border-radius: 50%;
  transition: left .2s;
  position: absolute;
}

#infinite-mode-input:checked + .infinite-mode-label:after {
  left: calc(.75 * 2.5 * var(--infinite-mode-label-font-size));
  transform: translateX(-50%);
}

#infinite-mode-input:checked + .infinite-mode-label {
  color: var(--infinite-mode-label-checked-color);
}
